  display: none;
}
.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

:root {
  /* -- Fonts --*/
  --ff-inter: "Inter", sans-serif;
  --ff-lora: "Lora", sans-serif;
  --bg-body: rgba(13, 14, 14, 1);
  --bg-footer: linear-gradient(180deg, #101212 0%, #151818 100%);
  --home-fade: linear-gradient(360deg, #0D0E0E 9.91%, rgba(13, 14, 14, 0) 42.73%);
  --card-fade: linear-gradient(360deg, rgba(22, 26, 25, 0.8) 0%, rgba(22, 26, 25, 0) 69.38%);
  --news-card-heading: hsla(228, 3%, 68%, 1);
  --comm-block-fade: linear-gradient(90.04deg, #161A19 0.04%, rgba(22, 26, 25, 0) 58.61%);
  --bg-features-card: hsla(169, 7%, 9%, 1);
  --bg-btn: linear-gradient(360deg, #51140C 0%, #9B2818 4.69%, #E93D25 4.7%, #B11414 95.77%, #DB3A24 95.83%, #E24523 99.81%);
  --bg-hover-btn: linear-gradient(360deg, #51140C 0%, #9B2818 4.69%, #792419 4.7%, #8e1b1b 95.77%, #802b20 95.83%, #831d06 99.81%);
  --clr-light-400: hsla(0, 0%, 100%, 1);
  --clr-light-300: hsla(228, 3%, 68%, 1);
  --bg-offer-btn: linear-gradient(0.48deg, #E93D25 -10.64%, #B11414 94.89%, #DB3A24 94.95%, #E24523 99.33%);
  --header-shadow: 0px 8px 48px 0px hsla(0, 0%, 0%, 0.08);
  --bg-header: linear-gradient(180deg, #101212 0%, #161919 100%),
  linear-gradient(0deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
  --r-gradient: linear-gradient(180deg, #E93D25 0%, #D32525 100%),
  linear-gradient(0deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
  --bg-offline-icon: radial-gradient(50% 50% at 50% 50%, rgba(255, 151, 77, 0.08) 0%, rgba(255, 77, 77, 0) 100%);
  --bg-soon-icon: radial-gradient(50% 50% at 50% 50%, rgba(232, 193, 177, 0.08) 0%, rgba(23, 26, 26, 0.08) 100%);
  --bg-online-icon: radial-gradient(50% 50% at 50% 50%, rgba(80, 255, 77, 0.08) 0%, rgba(80, 255, 77, 0) 100%);
  --ls: 0.01em;
  --gr-clr: 172, 173, 177;
  --fb-clr: 24, 119, 242;
  --disc-clr: 88, 101, 242;
  --steam-clr: 41, 46, 62;
  --w-clr: 255, 255, 255;
  --pr-clr: 22, 26, 25;
  --twitch-clr: 145, 70, 255;
  --r-clr: 233, 61, 37;
  --invalid-state-text-clr: 234, 76, 55;
  --invalid-state-bg: 233, 61, 37;
  --testing-state-text-clr: 248, 198, 69;
  --testing-state-bg: 234, 184, 55;
  --pending-state-text-clr: 172, 173, 177;
  --pending-state-bg: 108, 108, 108;
  --confirmed-state-text-clr: 255, 108, 46;
  --confirmed-state-bg: 234, 116, 31;
  --fixed-state-text-clr: 38, 212, 76;
  --fixed-state-bg: 19, 165, 51;
  --d-clr: 12, 14, 13;
  --roadmap-container-h: 27.375rem;
  --icon-ratio: 3.625rem;
}

.main-container {
  padding: 0 clamp(15px, 1.25rem, 20px);
  max-width: calc(86.25rem + clamp(15px, 1.25rem, 20px));
  margin-inline: auto;
  width: 100%;
}
@media only screen and (max-width: 81.25em) {
  .main-container {
    max-width: calc(75rem + clamp(15px, 1.25rem, 20px));
  }
}
@media only screen and (max-width: 71.875em) {
  .main-container {
    max-width: calc(50rem + clamp(15px, 1.25rem, 20px));
  }
}
@media only screen and (max-width: 48em) {
  .main-container {
    padding: 0 0.9375rem;
    max-width: calc(34.375rem + clamp(15px, 1.25rem, 20px));
  }
}

.section-margin {
  margin-bottom: 8.75rem;
}
@media only screen and (max-width: 48em) {
  .section-margin {
    margin-bottom: 4.375rem;
  }
}

.btn {
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  letter-spacing: var(--ls);
  background: var(--bg-btn);
  padding: 0.625rem 1rem;
  position: relative;
  transition: 200ms ease;
}
.btn:hover {
  background: var(--bg-hover-btn);
}
.btn:hover span {
  filter: brightness(100%);
}
.btn span {
  font-size: 1rem;
  font-weight: 500;
  position: relative;
  z-index: 10;
  color: rgba(var(--w-clr), 1);
}
.btn::after {
  position: absolute;
  content: "";
  background: rgba(0, 0, 0, 0.35);
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: 400ms ease;
  top: 0;
  left: 0;
  z-index: 7;
}
.btn:hover::after {
  opacity: 0;
  visibility: hidden;
}
.btn::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("../img/btn/red-btn-overlay.png");
  z-index: 4;
  background-size: 100% 100%;
}

body {
  margin: 0;
  padding: 0;
  color: var(--clr-light-400);
  background-color: var(--bg-body);
  width: 100%;
  position: relative;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  letter-spacing: var(--ls);
}
body::before {
  position: absolute;
  content: "";
  width: 100%;
  background-color: rgba(13, 14, 14, 0.8);
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 500;
  transition: 400ms ease;
  visibility: hidden;
  pointer-events: none;
}

.body-locked {
  overflow: hidden;
}
.body-locked::before {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.sparks-wrapper {
  position: absolute;
  width: 100%;
  z-index: 10;
  display: none;
}

p {
  color: hsl(228, 3%, 68%);
  font-size: 1rem;
  line-height: 150%;
  letter-spacing: var(--ls);
}
@media only screen and (max-width: 71.875em) {
  p {
    font-size: 0.875rem;
  }
}

html {
  box-sizing: border-box;
  width: 100%;
  font-family: var(--ff-inter);
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

main {
  flex: 1 1 auto;
}

table {
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  color: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
}

button {
  background-color: transparent;
  border: none;
  display: inline-flex;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: initial;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

input {
  width: 100%;
  outline: none;
  border: none;
}

label {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.main-padding {
  padding-top: clamp(70px, 8.75vw, 140px);
  position: relative;
}
.main-padding h1 {
  position: relative;
  z-index: 5;
}
.main-padding::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 8.75rem;
  left: 0;
  width: 100%;
  background: linear-gradient(180deg, rgba(13, 14, 14, 0.6) 0%, rgba(13, 14, 14, 0) 100%);
}

.main-title {
  font-size: 4rem;
  font-weight: 600;
  font-family: var(--ff-lora);
  letter-spacing: -0.01em;
  margin-bottom: 1.5rem;
  text-align: center;
}
@media only screen and (max-width: 48em) {
  .main-title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
}

.section-title {
  font-size: clamp(1.625rem, 2vw, 32px);
  font-weight: 600;
  font-family: var(--ff-lora);
  margin-bottom: 2rem;
}

.header {
  position: relative;
  min-height: 4rem;
  display: flex;
  align-items: center;
  background: var(--bg-header);
  box-shadow: var(--header-shadow);
  border-bottom: 1px solid rgba(var(--w-clr), 0.1);
}
.header__options {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  grid-gap: 1.125rem;
}
.header__burger {
  display: none;
  grid-gap: 0.4375rem;
}
.header__burger span {
  border-radius: 5px;
  display: block;
  width: 1.75rem;
  height: 2px;
  background-color: var(--clr-light-400);
}
.header__login {
  letter-spacing: var(--ls);
  font-weight: 500;
  transition: 200ms ease;
}
.header__login:hover {
  color: var(--clr-light-400);
}
.header__row {
  max-width: 66.5rem;
  margin: 0 auto;
}
.header__logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 300ms ease;
}
.header__logo:hover {
  transform: translate(-50%, -50%) scale(1.04);
}
.header__navigation nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__navigation ul {
  display: flex;
  align-items: center;
}
.header__navigation ul li a, .header__navigation ul li span {
  padding: 1.25rem 2rem;
  color: var(--clr-light-300);
  font-weight: 500;
  line-height: 150%;
  transition: 300ms ease;
  cursor: pointer;
}
.header__navigation ul li a:hover, .header__navigation ul li span:hover {
  color: var(--clr-light-400);
}

.nav__arrow {
  display: block;
  width: 0.4375rem;
  transition: 400ms ease;
  height: 0.375rem;
}
.nav__link_dropdown-link {
  padding: 20px 32px !important;
  display: flex;
  align-items: center;
  grid-gap: 0.4375rem;
}
.nav__link_dropdown-link:hover img {
  transform: scaleY(-1);
}
.nav__item_dropdown {
  position: relative;
}
.nav__item:hover .dropdown {
  transform: translateY(0);
  pointer-events: all;
  opacity: 1;
}

@media only screen and (max-width: 85.375em) {
  .header__row {
    max-width: 46.875rem;
  }
  .header__logo {
    max-width: 7.5rem;
    left: 50%;
  }
  .header nav {
    justify-content: space-between !important;
  }
  .header nav ul a {
    padding: 0.625rem 1rem;
    font-size: 0.9375rem;
  }
}
@media only screen and (max-width: 71.875em) {
  .header {
    min-height: 3.125rem;
  }
  .header__navigation {
    display: none;
  }
  .header__options {
    display: none;
  }
  .header__row {
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__logo {
    transform: translate(0);
    position: initial;
  }
  .header__logo:hover {
    transform: translate(0);
  }
  .header__burger {
    display: grid;
