}
.notes__author {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 0.5rem;
}
.notes__author-icon {
  width: var(--author-icon-ratio);
  height: var(--author-icon-ratio);
  flex: 0 0 auto;
  border-radius: 50%;
  background-color: rgba(var(--w-clr), 0.5);
}
.notes__author-icon img, .notes__author-icon svg, .notes__author-icon picture {
  width: inherit;
  height: inherit;
  border-radius: inherit;
}
.notes__author-name {
  color: rgba(var(--gr-clr), 1);
  font-weight: 500;
  letter-spacing: var(--ls-base);
  line-height: 1.5rem;
}

.delete {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 0.5rem;
}
@media (hover: hover) and (pointer: fine) {
  .delete:hover {
    opacity: 0.6;
  }
}
.delete span {
  color: rgba(var(--r-clr), 1);
  font-size: 0.875rem;
  letter-spacing: 0.01rem;
}
@media only screen and (min-width: 48em) {
  .delete span {
    font-size: 1rem;
  }
}

@media only screen and (min-width: 48em) {
  .notes {
    --block-p: clamp(1rem, 1.5vw, 1.5rem);
    --block-mb: 2.5rem;
  }
  .notes__col {
    grid-gap: var(--cols-gap);
  }
}
.collapse .notes__block-name::after {
  transform: translateY(-50%) rotate(90deg) scaleX(-1);
}

.wrapper-form {
  padding: 0 1rem;
}

.form {
  --inputs-gap: 1rem;
  --ls: 0.01rem;
  --clear-ratio: 1.5rem;
  margin-bottom: 2.5rem;
  max-width: 50.125rem;
  width: 100%;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
.form .dropdowns__item {
  color: rgba(var(--gr-clr), 1);
}
.form__close-btn {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background-color: rgb(12, 14, 13);
  border: 1px solid rgba(var(--w-clr), 0.15);
  z-index: 2;
  top: 0;
  right: 0;
  transition: 300ms ease;
}
@media (hover: hover) and (pointer: fine) {
  .form__close-btn:hover {
    filter: brightness(120%);
  }
}
.form__close-btn span {
  width: 1rem;
  height: 1px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(var(--rotate));
  background-color: rgba(var(--gr-clr), 1);
}
.form__close-btn span:nth-child(1) {
  --rotate: 45deg;
}
.form__close-btn span:nth-child(2) {
  --rotate: -45deg;
}
.form__heading {
  text-align: center;
}
.form__head {
  font-size: clamp(1.5rem, 2vw, 2rem);
  font-weight: 600;
  margin-bottom: 1rem;
}
.form__inner > *:not(:last-child) {
  margin-bottom: 2rem;
}
.form__inputs {
  display: flex;
  flex-direction: column;
  grid-gap: var(--inputs-gap);
  margin-bottom: 2rem;
}
.form__inputs-paper {
  width: 1.25rem;
  height: 1.25rem;
}
.form__inputs-item {
  position: relative;
}
.form__inputs-item textarea {
  min-height: 8.75rem;
  resize: vertical;
  outline: none;
}
.form__inputs-item textarea:focus-visible, .form__inputs-item input:focus-visible {
  border: 1px solid rgba(var(--w-clr), 0.2);
}
.form__inputs-item > input, .form__inputs-item textarea {
  padding: 1rem;
  border: 1px solid rgba(var(--w-clr), 0.04);
  background-color: rgba(var(--w-clr), 0.02);
  letter-spacing: var(--ls);
  color: rgba(var(--w-clr), 1);
  transition: 200ms ease;
  width: 100%;
}
.form__inputs-item > input::placeholder, .form__inputs-item textarea::placeholder {
  color: rgba(var(--w-clr), 0.4);
}
.form__inputs-item input {
  padding: 1rem calc(1.5rem + var(--clear-ratio)) 1rem 1rem !important;
}
.form__pattern {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1rem;
  justify-content: center;
  text-align: center;
  color: rgba(var(--gr-clr), 1);
  letter-spacing: var(--ls);
}
.form__pattern-line {
  background: linear-gradient(270deg, rgba(var(--w-clr), 0.1) 4.65%, rgba(var(--w-clr), 0) 90.41%);
  height: 1px;
  display: block;
  flex: 1;
}
.form__pattern-line--right {
  transform: scaleX(-1);
}
.form__clear {
  position: absolute;
  background-color: rgba(var(--r-clr), 1);
  border-radius: 50%;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  width: var(--clear-ratio);
  height: var(--clear-ratio);
  z-index: 2;
  transition: 200ms ease;
}
@media (hover: hover) and (pointer: fine) {
  .form__clear:hover {
    opacity: 0.8;
  }
}
.form__clear:active {
  opacity: 0.6;
}
.form__clear span {
  position: absolute;
  width: 0.875rem;
  height: 2px;
  background-color: rgba(var(--w-clr), 1);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(var(--clear-bar-rotate));
}
.form__clear span:nth-child(1) {
  --clear-bar-rotate: 45deg;
}
.form__clear span:nth-child(2) {
  --clear-bar-rotate: -45deg;
}
.form__add {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 0.75rem;
  justify-content: center;
  background-color: rgba(var(--w-clr), 0.04);
  border: 1px solid rgba(var(--w-clr), 0.1);
  padding: 1rem clamp(1.125rem, 1.25vw, 1.25rem);
  color: rgba(var(--gr-clr), 1);
  letter-spacing: var(--ls);
  font-weight: 500;
  transition: 300ms ease;
}
@media (hover: hover) and (pointer: fine) {
  .form__add:hover {
    background-color: rgba(var(--w-clr), 0.06);
    border: 1px solid rgba(var(--w-clr), 0.2);
  }
}
.form__add:active {
  background-color: rgba(var(--w-clr), 0.01);
}
.form__dd-file {
  border: 1px dashed rgba(var(--w-clr), 0.1);
  padding: 2rem 1.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0;
  cursor: pointer;
}
@media (hover: hover) and (pointer: fine) {
  .form__dd-file:hover {
    background-color: rgba(var(--w-clr), 0.08);
  }
}
.form__dd-file:active {
  background-color: rgba(var(--w-clr), 0.06);
}
.form__dd-file-drop {
  color: rgba(var(--gr-clr), 1);
  font-weight: 500;
}
.form__dd-file-max {
  font-size: 0.875rem;
  color: rgba(var(--gr-clr), 0.4);
}
.form__dd-file img {
  margin-bottom: 0.5rem;
}
.form__dd-file span {
  line-height: 1.5rem;
  letter-spacing: var(--ls);
}
.form__dd-file input[type=file] {
  opacity: 0;
  position: absolute;
  z-index: -1;
  width: auto;
}
.form__agreement {
  align-items: flex-start;
}
.form__buttons {
  justify-content: center;
}