.cards {
  --lock-ratio: 1.25rem;
  --lock-bg: linear-gradient(180deg, #E93D25 0%, #D32525 100%);

  display: flex;

  padding-top: 1.125rem;

  border-top: 1px solid rgba(var(--w-clr), .06);

  min-height: inherit;

  &__item {
    max-width: fit-content;

    position: relative;

    transform: rotate(-90deg) translateX(calc(-1 * (var(--roadmap-container-h) / 2.85))) !important;

    margin: 0 -9rem 0;

    min-width: var(--roadmap-container-h);

    &--active {
      transform: rotate(0) translateX(0) !important;

      // width: 27.375rem;

      margin: 0 .5rem 0;

      .cards {
        &__dropdown {
          display: block;

          max-height: initial;
        }
        &__inner {
          padding: clamp(5.5rem, 6.5vw, 6.5rem) clamp(1.25rem, 2vw, 2rem) clamp(1.25rem, 2vw, 2rem) clamp(1.25rem, 2vw, 2rem);

          flex-direction: column;
          justify-content: center;
        }
        &__info {
          text-align: center;
        }
        &__close {
          top: 1.125rem;
          right: 1rem;

          span {
            &:nth-child(1) {
              --rotate: 45deg;
            }
            &:nth-child(2) {
              --rotate: -45deg;
            }
          }
        }
        &__locked {
          --lock-ratio: 2.5rem;
        }
        &__icon {
          --icon-ratio: 8.75rem;

          position: absolute;

           left: 50%;
           top: calc(-1 * (var(--icon-ratio) / 2));

           transform: translateX(-50%);
        }
      }
    }
  }
  &__inner {
    padding: 2rem 5rem 2rem 2rem;

    box-shadow: 0rem 0.5rem 3rem 0rem rgba(0, 0, 0, 0.08);

    background-color: rgba(var(--pr-clr), 1);

    text-align: center;

    border: 1px solid rgba(var(--w-clr), .05);

    display: flex;
    align-items: center;
    grid-gap: 1.5rem;

    position: relative;
  }
  &__close {
    --btn-ratio: 3rem;
    --bar-w: 23px;
    --bar-h: 1px;

    width: var(--btn-ratio);
    height: var(--btn-ratio);

    border: 1px solid rgba(var(--w-clr), .2);

    border-radius: 50%;

    position: absolute;

    transition: 200ms ease;

    top: 2.5rem;
    right: 2rem;

    z-index: 1;

    @include hover {
      &:hover {
        transform: scale(1.05);
      }
    }

    span {
      display: block;

      background-color: rgba(var(--w-clr), 1);

      width: var(--bar-w);
      height: var(--bar-h);

      top: 50%;
      left: 50%;

      position: absolute;

      transform: translate(-50%, -50%) rotate(var(--rotate));

      &:nth-child(1) {
        --rotate: 90deg;
      }
      &:nth-child(2) {
        --rotate: -180deg;
      }
    }
  }
  &__locked {
    top: 0;
    right: 0;

    width: var(--lock-ratio);
    height: var(--lock-ratio);

    background-image: var(--lock-bg);

    border-radius: 50%;

    position: absolute;

    @include dfc;

    img, svg {
      max-width: 0.4375rem;
    }
  }
  &__dropdown {
    max-height: 0;

    overflow: hidden;

    display: none;
  }
  &__date {
    color: rgba(var(--gr-clr), 1);
    letter-spacing: var(--ls);
  }
  &__info {
    text-align: left;
  }
  &__icon {
    @include ratio(var(--icon-ratio));
    display: grid;
    place-items: center;
    border-radius: 50%;
    border: 1px solid rgba(var(--w-clr), 0.1);

    position: relative;

    flex: 0 0 auto;

    &-img {
      border-radius: 50%;
      overflow: hidden;
      width: 100%;
      height: 100%;

      padding: 0.125rem;
    }
  }
  &__card-name {
    margin-bottom: 8px;
  }
  &__name {
    font-family: var(--ff-lora);
    font-size: clamp(18px, 1.5vw, 24px);
    margin-bottom: 9px;

    @include hide-text(1);
  }

  .dropdowns {
    grid-template-columns: 100%;
    margin-top: 2rem;

    &__item {
      background: linear-gradient(180deg, #212424 0%, #252828 100%);
    }
    &__item-selected {
      p {
        color: rgba(var(--gr-clr), 1);

        @include fl(row, center, 0.5rem);

        span {
          opacity: .4;

          max-width: 9rem;

          display: block;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
    &__hidden {
      margin: 0 -1rem 0;
    }
    &__hidden-inner {
      padding: 1rem;

      border: 1px solid rgba(var(--w-clr), .04);

      background-color: rgba(var(--w-clr), .02);

      margin: 0.375rem 0rem -1rem;

      max-height: 28rem;

      overflow-y: scroll;

      &::-webkit-scrollbar {

        background-color: rgba(var(--w-clr), .1);
      }
      &::-webkit-scrollbar-track {
        box-shadow: inset 0 0 2px rgba(var(--w-clr), .4);
      }
      &::-webkit-scrollbar-thumb {
        background:rgba(#000, .5);
      }
    }
    &__content {
      background: rgba(22, 26, 25, 1);

      padding: 1rem;

      text-align: left;

      & > *:not(:last-child) {
        margin-bottom: 1rem;
      }

      &-heading {
        color: rgba(var(--w-clr), 1);
      }
      &-image {
        img, picture, svg {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}

@include max-width($mobMq) {
  .roadmap {
    margin-bottom: 4.375rem;
  }
}
